.main-nav {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 21px 30px 22px;
  border-top: $border;
  border-bottom: $border;
  flex-wrap: wrap;
}

.main-nav__logo {
  line-height: 0;
  margin-right: calc(50% - 53px);
}

.main-nav__toggle {
  font-size: 0;
  line-height: 0;
  border: none;
  background: url("../img/svg_inline/icon-menu-close.svg") no-repeat center center;
  width: 20px;
  height: 20px;
}

.main-nav__search-input,
.main-nav__delivery {
  display: none;
}

.main-nav__search-btn {
  & svg {
    fill: $fill-svg-black;
  }
}

@media (max-width: $mobile-width-only) {
  .main-nav {
    flex-direction: column;
    padding: 0;

    &--closed &__toggle {
      background: url("../img/svg_inline/icon-menu-open.svg") no-repeat center center;
      width: 20px;
      height: 14px;
    }

    &--closed &__logo {
      border-bottom: none;
    }

    &--closed .product-list,
    &--closed .main-nav__search,
    &--closed .user-nav {
      display: none;
    }
  }

  .main-nav__search {
    width: 100%;
    padding: 30px 0;
    border-bottom: $border;
    position: relative;

    & svg {
      position: absolute;
      left: calc(50% - 130px);
      fill-opacity: 0.2;
      fill: #231f20;
    }
  }

  .main-nav__search-btn {
    border: none;
    background: transparent;
    font-weight: 700;
    color: $color-gray;
    font-size: 17px;
    padding: 0;
    padding-left: calc(50% - 85px);
  }

  .main-nav__toggle {
    position: absolute;
    top: 31px;
    right: 30px;
  }

  .main-nav__logo {
    margin-right: 0;
    padding: 21px 0;
    width: 100%;
    text-align: center;
    border-bottom: $border;
  }
}

@media (min-width: $tablet-width) {
  .main-nav {
    padding: 0 50px;
    flex-wrap: wrap;
    line-height: 0;
    position: relative;
  }

  .main-nav__logo {
    margin: 0;
    min-width: 320px;
    padding: 20px 0;
    order: 2;
    cursor: pointer;
  }

  .main-nav__search {
    display: flex;
    justify-content: center;
    align-items: center;
    border-right: $border;
    border-left: $border;
    margin-right: auto;
    order: 3;
    min-height: 46px;
    width: 82px;
    padding: 20px 0;
  }

  .main-nav__search-btn {
    font-size: 0;
    line-height: 0;
    border: none;
    cursor: pointer;
    background: none;
    outline: none;

    & svg {
      opacity: 0.2;

      &:hover {
        fill: $color-green;
        opacity: 1;
      }

      &:active {
        opacity: 0.3;
      }
    }
  }

  .main-nav__delivery {
    display: flex;
    width: 100%;
    justify-content: flex-end;
    text-decoration: none;
    color: $color-gray;
    margin-left: auto;
    position: absolute;
    top: calc(100% + 30px);
    z-index: 10;
  }

  .main-nav__toggle {
    display: none;
  }
}

@media (min-width: $desktop-width) {
  .main-nav {
    height: 1px;
    min-height: 88px;
    justify-content: flex-start;
  }

  .main-nav__logo {
    min-width: auto;
    padding: 0;
    margin-left: 62px;
    margin-right: auto;

    &:hover {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }
  }

  .main-nav__search {
    margin: 0;
  }

  .main-nav__delivery {
    right: 50px;
  }
}
